import { Modal } from "../index";

const InputFormatModal = ({ isOpen, onClose }) => {
  return (
    <Modal
      isOpen={isOpen("inputFormat")}
      onClose={() => onClose("inputFormat")}
      title="入力形式について"
    >
      <div>
        入力形式が表示されます。
      </div>
    </Modal>
  );
};

export default InputFormatModal;
